.bigArticle {
  flex-grow: 1;

  @media screen and (min-width: 1440px) {
    & > section:not(:first-of-type) {
      max-width: 1272px;
      margin-left: auto;
      margin-right: auto;
    }

    & > section:first-of-type {
      width: 100%;
    }
  }

  & > .header {
    position: relative;

    .header {
      &__contentWrapper {
        max-width: 1056px;
        margin: 0 auto;

        & > .value {
          color: white;
          position: absolute;
          bottom: 40px;
          z-index: 1;

          @media screen and (min-width: 1440px) {
            max-width: 1056px;
          }

          @media screen and (min-width: 320px) and (max-width: 1439px) {
            margin: 0 20px;
            width: calc(100% - 40px);
          }

          .value__heading {
            font-family: "PT Root UI Bold";

            @media screen and (min-width: 1440px) {
              font-size: 36px;
            }

            @media screen and (min-width: 768px) and (max-width: 1439px) {
              font-size: 30px;
            }

            @media screen and (min-width: 320px) and (max-width: 767px) {
              font-size: 24px;
            }
          }

          .value__stats {
            margin-bottom: 24px;
            @media screen and (max-width: 450px) {
              .feedInfo {
                flex-direction: column;
              }
            }
          }

          .tags {
            display: flex;
            overflow-y: auto;

            & > .tag {
              margin-right: 4px;
              flex-shrink: 0;
              background-color: rgba(255, 255, 255, 0.2);

              .tag__name {
                color: #FFFFFF;
              }
            }
          }

          .feedInfo {
            justify-content: space-between;
            margin-bottom: 22px;

            svg path {
              fill: #FFFFFF;
            }
          }
        }
      }

      &__image_wrapper {
        position: relative;
        width: 100vw;
        &:after {
          content: '';
          position: absolute;
          bottom: 0;
          right: 0;
          width: 100%;
          height: 100%;
          background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 1));
        }
      }

      &__mainImage {
        margin: 0 calc(50% - 50vw);
        overflow: auto;
        img {
          width: 100%;
          height: 100%;
          max-height: 810px;
          object-fit: cover;
        }



        @media screen and (min-width: 1440px) {
          img {
            height: 810px;
          }
        }

        @media screen and (min-width: 768px) and (max-width: 1439px) {
          height: 650px;
        }

        @media screen and (min-width: 320px) and (max-width: 767px) {
          height: 490px;
        }
      }
    }
  }

  & > .mainPart {
    display: flex;
    justify-content: space-between;
    @media screen and (min-width: 1440px) {
      margin: 36px auto 0;

      .category {
        margin-left: 16px;
      }

      .article {
        & > section:not(:last-of-type) {
          margin-bottom: 36px;
        }

        margin-right: 108px;

        &_centered {
          margin-left: 108px;
        }

        &_cardAside {
          display: flex;
          margin-bottom: 36px;

          .feedCard {
            width: 300px;
            flex: 1 0 auto;
            margin-right: 24px;
          }
        }
      }

      .aside {
        width: 300px;
        flex-shrink: 0;
        flex-grow: 0;

        display: grid;
        grid-template-rows: 200px 200px 432px 656px 200px 200px 432px;
        grid-template-columns: 300px;
        grid-row-gap: 24px;
      }

      .mainContent {
        flex-grow: 0;
        margin-right: 24px;

        &__heading {
          font-size: 36px;
          margin: 8px 0px;
        }
      }
    }

    @media screen and (min-width: 768px) and (max-width: 1439px) {
      flex-direction: column;
      margin: 20px auto 0;

      .category {
        margin-left: 16px;
      }

      .article {
        & > section:not(:last-of-type) {
          margin-bottom: 36px;
        }

        margin-right: 48px;

        &_centered {
          margin-left: 48px;
        }

        &_cardAside {
          display: flex;
          margin-bottom: 36px;

          .feedCard {
            width: 300px;
            flex: 1 0 auto;
            margin-right: 24px;
          }
        }
      }

      .aside {
        flex-shrink: 0;
        flex-grow: 0;

        margin: 0 20px 50px 20px;
        display: grid;
        grid-gap: 24px;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 200px 500px;

        .newsline {
          grid-column-start: 1;
          grid-column-end: 3;
          grid-row-start: 2;
          grid-row-end: 3;
          margin: 20px;
          border-radius: 8px;
        }

        .advertisement {
          &:nth-of-type(1) {
            grid-column-start: 1;
            grid-column-end: 3;
            grid-row-start: 1;
            grid-row-end: 2;
          }

          &:nth-of-type(2) {
            grid-column-start: 3;
            grid-column-end: 4;
            grid-row-start: 1;
            grid-row-end: 2;
          }

          &:nth-of-type(3) {
            grid-column-start: 3;
            grid-column-end: 4;
            grid-row-start: 2;
            grid-row-end: 3;
          }
        }
      }

      .mainContent {
        flex-grow: 0;
        margin: 0 20px;

        &__heading {
          font-size: 36px;
          margin: 8px 0px;
        }
      }
    }

    @media screen and (min-width: 320px) and (max-width: 767px) {
      flex-direction: column;

      .category {
        margin-left: 16px;
      }

      .article {
        .quote {
          width: 100vw;
          position: relative;
          left: 50%;
          right: 50%;
          margin-left: -50vw;
          margin-right: -50vw;
          flex: 1 0 auto;

          padding: 24px 8px;
        }

        & > section:not(:last-of-type) {
          margin-bottom: 36px;
        }

        &_cardAside {
          display: flex;
          flex-direction: column;
          margin-bottom: 36px;

          .feedCard {
            width: 100vw;
            position: relative;
            left: 50%;
            right: 50%;
            margin-left: -50vw;
            margin-right: -50vw;
            flex: 1 0 auto;
          }
        }
      }

      .aside {
        flex-shrink: 0;
        flex-grow: 0;

        margin: 0 20px 50px 20px;
        display: grid;
        grid-gap: 24px;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 200px 500px;

        .newsline {
          grid-column-start: 1;
          grid-column-end: 4;
          grid-row-start: 2;
          grid-row-end: 3;
          margin: 20px;
          border-radius: 8px;
        }

        .advertisement {
          display: none;

          &:nth-of-type(1) {
            display: flex;
            grid-column-start: 1;
            grid-column-end: 4;
            grid-row-start: 1;
            grid-row-end: 2;
          }

          &:nth-of-type(2) {
            display: flex;
            grid-column-start: 1;
            grid-column-end: 4;
            grid-row-start: 2;
            grid-row-end: 4;
          }
        }
      }

      .mainContent {
        flex-grow: 0;
        margin: 0 20px;

        &__heading {
          font-size: 24px;
          margin: 8px 0px;
        }
      }
    }

    .tags {
      display: flex;

      .tag {
        margin-right: 4px;
      }
    }

    .text {
      color: #282C3B;

      &__image {
        width: 100%;

        img {
          object-fit: cover;
          width: 100%;
        }

        @media screen and (min-width: 320px) and (max-width: 767px) {
          width: 100vw;
          position: relative;
          left: 50%;
          right: 50%;
          margin-left: -50vw;
          margin-right: -50vw;
          flex: 1 0 auto;
        }
      }

      &__header {
        font-size: 24px;
        font-family: "PT Root UI Medium";
        @media screen and (min-width: 320px) and (max-width: 767px) {
          font-size: 18px;
        }
      }

      &__subtext {
        font-size: 20px;
        font-family: "PT Root UI Regular";
        @media screen and (min-width: 320px) and (max-width: 767px) {
          font-size: 16px;
        }
      }

      &__sliderCard {
        @media screen and (min-width: 320px) and (max-width: 767px) {
          width: 100vw;
          position: relative;
          left: 50%;
          right: 50%;
          margin-left: -50vw;
          margin-right: -50vw;
          flex: 1 0 auto;
        }
      }
    }

    .mainContent {
      .feedInfo {
        justify-content: space-between;
        color: #9B9FAD;

        svg path {
          fill: #282C3B;
        }
      }

      & > section {
        margin-bottom: 48px;
      }

      .ads {
        display: flex;

        & > .advertisement {
          &:first-of-type {
            flex-grow: 70;
            margin-right: 24px;
          }

          &:last-of-type {
            flex-grow: 22;
          }
        }
      }

      &__heading {
        font-family: "PT Root UI Bold";
      }

      &__mainImage {
        margin-bottom: 14px;
      }

      &__comments {
        & > .comments {
          & > .menuUnderlineSwitcher {
            .menuUnderlineSwitcher__item {
              font-size: 16px;
              font-family: "PT Root UI Regular";
              margin-bottom: 12px;
            }
          }
        }
      }
    }
  }

  &__recommendedArticles {
    margin-bottom: 70px;
  }

  & > .ads {
    display: grid;
    grid-template-columns: 300px 300px 624px;
    grid-template-rows: 200px;
    grid-column-gap: 24px;

    margin-bottom: 45px;

    @media screen and (min-width: 320px) and (max-width: 1439px) {
      display: none;
    }
  }
}